import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { BrowserModule }  from '@angular/platform-browser';
import { FileUploadModule } from 'ng2-file-upload';
import { RouterModule } from "@angular/router";
import { FormsModule } from "@angular/forms";
import { NgxEchartsModule } from "ngx-echarts";


import { HomeRoutes } from "./home.routes";
import { HomeComponent } from "./home.component";


import { ChatroomComponent } from "../chatroom/chatroom.component";
import { HomeIndexComponent } from '../home-index/home-index.component';
import { BabyRoomComponent } from '../baby-room/baby-room.component';
import { DemoRoomComponent } from '../demo-room/demo-room.component';
import { MessageRoomComponent } from '../message-room/message-room.component';


@NgModule({
    imports: [
        CommonModule,
        FileUploadModule,
        FormsModule,
        NgxEchartsModule,
        RouterModule.forChild(<any>HomeRoutes)
    ],
    declarations: [
        HomeComponent,
        ChatroomComponent,
        HomeIndexComponent,
        BabyRoomComponent,
        DemoRoomComponent,
        MessageRoomComponent
    ]
})

export class HomeModule {}


