// 数据序列化
private toQueryString(obj) {
  let result = [];
  for (let key in obj) {
    key = encodeURIComponent(key);
    let values = obj[key];
    if (values && values.constructor == Array) {
      let queryValues = [];
      for (let i = 0, len = values.length, value; i < len; i++) {
        value = values[i];
        queryValues.push(this.toQueryPair(key, value));
      }
      result = result.concat(queryValues);
    } else {
      result.push(this.toQueryPair(key, values));
    }
 }
  return result.join('&');
}
private toQueryPair(key, value) {
  if (typeof value == 'undefined') {
    return key;
  }
  return key + '=' + encodeURIComponent(value === null ? '' : String(value));
}   

  // 上传图片获取图片链接
  upDateImg(file){
    return new Promise((resolve, reject) => {
      let headers = new Headers({
        'Content-Type': 'application/x-www-form-urlencoded'
      });
      let options = new RequestOptions({
        headers: headers
      });
      this.http.post("http://localhost:4400/upDataFile/upDateImg", this.toQueryString(file), options)
        .map(res => res.json())
        .subscribe(data => { resolve(data), error => { reject(error) } 
        this.upDateURL.push(data.imageUrl)
        console.log(this.upDateURL)
      })
    })
  }
  
  // 定义上传事件upDataFile,新增各类消息
  uploadFile() {
    const headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'});
    const options = new RequestOptions({ headers: headers});
    const body = new URLSearchParams();

    if(this.addSet.label === ""){
      this.errorTextIndex = 'show';
      return false
    }

    let setData = JSON.stringify(this.addSet);

    body.set('data' , setData);

    console.log(body)
    this.http.post('http://localhost:4400/upDataFile/upDataFile', body, options)
        .subscribe((res : Response) => {
          this.viewList = res.json().data;
          this.imgList = [];
          this.upDateURL = [];
          this.addSet = {
            title : '',
            content : '',
            label : '',
            url: this.upDateURL
          };
          this.currentDisplay = 'none';
          console.log(this.viewList)
        })
  }

