  // 时间差函数
  private timer;
  ngAfterViewInit() {
    // 时间差
    this.timer = setInterval(() => {
      this.oldTime = "2018/3/18 15:07:00"
      this.dateBegin = new Date(this.oldTime);
      this.dateEnd = new Date()
      let dateDiff = this.dateEnd.getTime() - this.dateBegin.getTime();//时间差毫秒数
      this.dayDiff = Math.floor( dateDiff/(24*3600*1000));  //时间差天数
      let leave1 = dateDiff%(24*3600*1000);  //计算天数之后的毫秒数
      this.hours = Math.floor( leave1/(3600*1000) ); //计算出小时数
      let leave2 = leave1%(3600*1000) //计算小时数之后的毫秒数
      this.minutes = Math.floor( leave2/(60*1000) ) //计算相差分钟数
      let leave3 = leave2%(60*1000);  //计算分数后的毫秒数
      this.seconds = Math.round(leave3/1000);
      if(this.hours < 10){
        this.hours = '0' + this.hours
      }
      if(this.minutes < 10){
        this.minutes = '0' + this.minutes
      }
      if(this.seconds < 10){
        this.seconds = '0' + this.seconds
      }
     }, 1000);
  }

  swiper() {
    var swiper = new Swiper('.swiper-container-H',{
      direction: 'vertical',
          slidesPerView: 1,
          spaceBetween: 0,
          mousewheel: true
          // pagination: {
          //   el: '.swiper-pagination-H',
          //   clickable: false,
          // }
    })


    
  }

  swiper_1() {
    var swiper2 = new Swiper('.swiper-container-W', {
      spaceBetween: 0,
      touchRatio :2,
      autoplay: {
        delay: 2500,
        disableOnInteraction: true,
      },
      pagination: {
        el: '.swiper-pagination-W',
        clickable: true,
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
    });
  }

