import { HomeComponent } from "./home.component";
import { ChatroomComponent } from "../chatroom/chatroom.component";
import { HomeIndexComponent } from '../home-index/home-index.component';
import { BabyRoomComponent } from '../baby-room/baby-room.component';
import { DemoRoomComponent } from '../demo-room/demo-room.component';
import { MessageRoomComponent } from '../message-room/message-room.component';

export const HomeRoutes = [
    {
        path: '',
        component: HomeComponent,
        children:[
            {
                path:'',
                component: HomeIndexComponent
            },
            {
                path:'chatRoom',
                component: ChatroomComponent
            },
            {
                path:'share',
                component: BabyRoomComponent
            },
            {
                path:'demoRoom',
                component: DemoRoomComponent
            },
            {
                path:'messageRoom',
                component: MessageRoomComponent
            }
        ]
    }
]


