import { Component, Renderer2,Inject , ViewChild, ElementRef, OnInit, AfterViewInit, trigger, state, style, transition, animate } from '@angular/core';
import { Http, Response, RequestOptions, Headers, URLSearchParams  } from '@angular/http';
import { DOCUMENT } from '@angular/platform-browser';

@Component({
  selector: 'app-message-room',
  templateUrl: './message-room.component.html',
  styleUrls: ['./message-room.component.scss'],
  animations: [
    trigger('alert', [
      state('block',style({
        'display' : 'block',
        'opacity' : '1'
      })),
      state('none',style({
        'display' : 'none',
        'opacity' : '0'
      })),
      transition('block => none', animate('600ms cubic-bezier(0.4, 0, 0.2, 1)')),
      transition('none => block', animate('600ms cubic-bezier(0.4, 0, 0.2, 1)'))
    ])
  ]
})
export class MessageRoomComponent implements OnInit, AfterViewInit {
  // 更换背景
  @ViewChild('messageBG') messageBG : ElementRef;
  // 滚动事件
  @ViewChild('alert') alert : ElementRef;

  // 表情验证
  @ViewChild('name') inputName : ElementRef;
  @ViewChild('email') inputEmail : ElementRef;
  @ViewChild('blog') inputBlog : ElementRef;


  public currentImg : any;      // 背景图片随机数

  // 动画用
  public fullDisplay : any;     // 弹框的显示和隐藏
  public errorDisplay : any;    // 空留言的错误提醒

  public messageName : any;     // 昵称
  public img : any;             // 留言头像
  public setMessageData : any;  // 新增留言数据
  public messageList : any;     // 留言板列表


  constructor(private ele: ElementRef, private http: Http,private _renderer2: Renderer2, @Inject(DOCUMENT) private _document) {
    this.fullDisplay = 'none';
    this.errorDisplay = 'none';
    this.messageName = '雁过留名';
    this.img = '../../../assets/images/touxiang.png';
    this.setMessageData = '';
   
   }

   ngAfterViewInit() {
    setInterval(() => {
      this.changeBG()
    },10000)
   }

   changeBG(): void{
     let imgArr=['14254.jpg','5510.jpg','11781.jpg','14529.jpg','11212.jpg','14414.jpg','14248.jpg','apic2278.jpg'];
     this.currentImg = Math.floor(Math.random()*8)
     this.messageBG.nativeElement.style.backgroundImage = "url(../../../assets/images/banner/"+imgArr[this.currentImg]+")"
   }

  ngOnInit() {   
    //  刚进页面拿到数据
    const body = new URLSearchParams();
    this.http.post('http://localhost:4400/messageLib/getMessageList', body)
        .subscribe((res: Response) => {
          let data = res.json();
          console.log(data)
        })
  }

  // 弹框显示影藏
  alertShowHide(): void{
    this.errorDisplay = 'none';
    if(this.fullDisplay === 'none'){
      this.fullDisplay = 'block';
      this.screen();

    }else if(this.fullDisplay === 'block'){
      this.fullDisplay = 'none'
    }
  }

  // 弹框出现判断位置
  screen(): void{
    let screenWidth = document.documentElement.clientWidth;
    let screenHeight = document.documentElement.clientHeight;
    let top = (screenHeight - 300)/2;
    let left = (screenWidth - 400)/2;
    this.alert.nativeElement.style.top = top + 'px';
    this.alert.nativeElement.style.left = left + 'px';
  }

  // 鼠标滚动事件
  mousewheel(e): void{
    console.log(e)
    let screenWidth = document.documentElement.clientWidth;
    let screenHeight = document.documentElement.clientHeight;
    let scrollTop = document.documentElement.scrollTop;
    let top = (screenHeight - 300)/2 + scrollTop;
    let left = (screenWidth - 400)/2;
    this.alert.nativeElement.style.top = top + 'px';
    this.alert.nativeElement.style.left = left + 'px';
  }
  
  // 保存昵称
  save(name: any, email: any, blog: any, Github: any) {
    let emailRegEx = new RegExp(/^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/);
    let GithubRegEx = new RegExp(/^[github.com]\+\/\?([^# ]\*)/);
    let BlogRegEx = new RegExp(/^(?=^.{3,255}$)[a-zA-Z0-9][-a-zA-Z0-9]{0,62}(\.[a-zA-Z0-9][-a-zA-Z0-9]{0,62})+$/)
  //  验证昵称
    if(name.value === '' || name.value === '昵称！昵称！要填！'){
      name.style.border = '1px solid #F6B603';
      name.style.color = '#F6B603';
      name.value = "昵称！昵称！要填！";
      return false
    }
     // 验证博客地址
     if(blog.value !== '' && !BlogRegEx.test(blog.value) || blog.value !== '' && blog.value === '这地址访问不到啊~'){
      blog.style.border = '1px solid #F6B603';
      blog.style.color = '#F6B603';
      blog.value = "这地址访问不到啊~";
      return false
    }
    // 验证Github
    if(Github.value !== '' && !GithubRegEx.test(email.value) || Github.value !== '' && Github.value === '回来！地址错了！'){
      Github.style.border = '1px solid #F6B603';
      Github.style.color = '#F6B603';
      Github.value = "回来！地址错了！";
      return false
    }
    // 验证邮箱
    if(email.value === '' || email.value === '大哥，邮箱地址！'){
      email.style.border = '1px solid #F6B603';
      email.style.color = '#F6B603';
      email.value = "大哥，邮箱地址！";
      return false
    }else if(!emailRegEx.test(email.value)){
      email.style.border = '1px solid #F6B603';
      email.style.color = '#F6B603';
      email.value = "回来！地址错了！";
      return false
    }else{
      const headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'});
      const options = new RequestOptions({ headers: headers});
      const body = new URLSearchParams();
      let address = sessionStorage.getItem('address')

      body.set('name', name.value);
      body.set('email', email.value);
      body.set('blog', blog.value);
      body.set('address', address);
      body.set('github', Github.value);
      setTimeout(() => {
        this.http.post("http://localhost:4400/messageLib/getEmail", body, options)
             .subscribe((res:Response) => {
               let data = res.json();
               this.setMessageData = data.data;
               this.messageName = data.data.name;
               this.img = data.data.img;
               this.fullDisplay = 'none';
               console.log(data)
              })
      }, 100); 

    }
  }

  // 表单验证失去焦点
  inputStyleBlur(e): void{
    e.target.style.border = "1px solid #ccc";
    e.target.style.color = '#333';
  }
  // 表单验证样式获取焦点
  inputStyleFocus(e): void{
    e.target.style.border = "1px solid #1C97DF";
    e.target.style.color = '#333';
    e.target.value = '';
  }
  // 发送留言
  sendMessage(message: any): void{
    if(message.value === ''){
      this.errorDisplay = 'block'
    }else{
      if(this.setMessageData === ''){
        this.fullDisplay = 'block';
        this.screen();
      }else{
        this.setMessageData.message = message.value;
        let body = new URLSearchParams();
        console.log(this.setMessageData);
        body.set('data', JSON.stringify(this.setMessageData));
        this.http.post('http://localhost:4400/messageLib/saveMessage', body)
            .subscribe((res: Response) => {
              let data = res.json();
              console.log(data)
            })
      }
    }
  }


}



